import React from "react";
import { NavLink } from "react-router-dom"; //gives active tag

export default function SignedOutLinks() {
  return (
    <ul className="right">
      <li>
        <NavLink to="/signup">SignUp</NavLink>
      </li>
      <li>
        <NavLink to="/signin">Login</NavLink>
      </li>
    </ul>
  );
}
